@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	max-width: 760px;
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	margin: 0 auto;
}


.post {
	padding: 12px;
	display: flex;
	box-shadow: 0 0 7px #607d8b;
	margin: 20px 0px;
	/* border: 1px solid #607d8b; */
}

.avatar {
	flex: 0 0 auto;
}

.oficial-icon {
	width: 24px;
	fill: #4caf50;
}

.avatar img {
	height: 50px;
	width: 50px;
	border-radius: 50%;
	display: block;
	object-fit: cover;
}

.post__container {
	padding-left: 10px;
}

.post__header {
	position: relative;
	font-size: 18px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-family: "Roboto", sans-serif;
}

.post__header-wrapper {
	display: flex;
}

.post-button-icon {
	width: 20px;
	cursor: pointer;
}

.post-button-icon:hover {
	fill: #4caf50;
}

.author-name {
	color: #2196f3;
	display: flex;
	flex-wrap: wrap;
	font-weight: 700;
}

.author-nickname {
	color: #9e9e9e;
	padding: 0px 5px;
}

.author-nickname::after {
	content: "·";
	padding-left: 5px;
}
.post__date {
	color: #9e9e9e;
}

.post__text {
	margin: 10px 0px;
	font-family: "Roboto", sans-serif;
	font-size: 18px;
	font-weight: 400;
}

.post__img {
	width: 100%;
	object-fit: cover;
	border-radius: 24px;
}

.post__footer {
	display: flex;
	justify-content: space-around;
	padding: 20px 0px;
}

.icons {
	width: 26px;
}
.post__answer,
.post__retweet,
.post__like,
.post__share {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.post__answer:hover .icons {
	fill: #2196f3;
}

.post__retweet:hover .icons {
	fill: #9c27b0;
}

.post__like:hover .icons {
	fill: #f44336;
}

.post__share:hover .icons {
	fill: #2196f3;
}

@media (max-width: 460px) {
	.post__header {
		font-size: 16px;
	}
	.post__container {
		padding-left: 5px;
	}
}
